import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";

import axios from "axios";

import Avatar from "@mui/material/Avatar";
import { makeStyles } from "@material-ui/core/styles";

// import Avatar from "@material-ui/core/Avatar";
const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    alignItems: "center",
    padding: "2%",
    objectFit: "cover",
    whiteSpace: "nowrap",
    overflow: "hidden",
    textOverflow: "ellipsis",
  },
  small: {
    width: theme.spacing(3),
    height: theme.spacing(3),
  },
  large: {
    width: theme.spacing(7),
    height: theme.spacing(7),
  },
}));

//      
const Profile = (userId) => {
  const classes = useStyles();
  const navigate = useNavigate();
  //   
  const [userData, setUserData] = useState({});

  // console.log(userId);
  // const a = userId.id;

  //    api  .
  useEffect(() => {
    axios.get(`/api/account/${userId.id}`).then(function (response) {
      setUserData(response.data);
    });
  }, []);

  return (
    <div className={classes.root}>
      {/* <Avatar src={} className={classes.large}></Avatar> */}
      {/* <div className={classes.root}> {userData.nickname}</div> */}
      <div className={classes.root}>
        <Avatar
          onClick={() => {
            navigate(`/user/${userId.id}`);
          }}
        >
          {/* <img src={it.image} alt={"  ."}></img> */}
        </Avatar>
      </div>
      <div className={classes.root}>
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            width: "100px",
          }}
        >
          <div
            style={
              {
                // display: "block",
              }
            }
            className={classes.root}
          >
            {userData.nickname}
          </div>
          <div
            style={{ color: "lightgrey", display: "block" }}
            className={classes.root}
          >
            {userData.memberState}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Profile;
