// /menu        
import { useNavigate } from "react-router-dom";

import MenuBar from "../components/MenuBar";
// import MyButton from "../components/MyButton";

import TopBar from "../components/TopBar";

const Menu = () => {
  const navigate = useNavigate();
  return (
    <div>
      <TopBar />
      {/* <div
        style={{
          height: "65px",
        }}
      ></div> */}
      <div
        onClick={() => {
          navigate("/storage");
        }}
      >
        <MenuBar leftChild={""} rightChild={">"} />
      </div>
      <div
        onClick={() => {
          navigate("/logbox");
        }}
      >
        <MenuBar leftChild={" "} rightChild={">"} />
      </div>
      <div
        onClick={() => {
          navigate("/menual");
        }}
      >
        <MenuBar leftChild={" "} rightChild={">"} />
      </div>
      <div
        onClick={() => {
          navigate("/map");
        }}
      >
        <MenuBar leftChild={" "} rightChild={">"} />
      </div>
    </div>
  );
};

export default Menu;
