import { useState, useRef, useEffect } from "react";

import axios from "axios";

import TopBar from "../components/TopBar";
import Card from "@mui/material/Card";
import { Avatar } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";
import Box from "@mui/material/Box";

//    style code
const useStyles = makeStyles((theme) => ({
  profileImage: {
    width: theme.spacing(15),
    height: theme.spacing(15),
    margin: "auto",
  },
}));

const SignUp = () => {
  //
  // 
  //   
  const classes = useStyles();

  //  
  const nickNameInput = useRef();
  const statusMessageInput = useRef();

  const [state, setState] = useState({
    image_file: "",
    preview_URL: "/assets/unknown.png",
    nickName: "",
    statusMessage: "",
  });

  let inputRef;

  const saveImage = (e) => {
    e.preventDefault();
    if (e.target.files[0]) {
      //    createObjectURL()    URL 
      URL.revokeObjectURL(state.preview_URL);
      const preview_URL = URL.createObjectURL(e.target.files[0]);
      setState(() => ({
        ...state,
        image_file: e.target.files[0],
        preview_URL: preview_URL,
      }));
    }
  };

  const deleteImage = () => {
    // createObjectURL()    URL 
    URL.revokeObjectURL(state.preview_URL);
    setState({
      ...state,
      image_file: "",
      preview_URL: "/assets/unknown.png",
    });
  };

  //   
  const handleChangeState = (e) => {
    setState({
      ...state,
      [e.target.name]: e.target.value,
    });
  };

  useEffect(() => {
    //   createObjectURL()    URL 
    return () => {
      URL.revokeObjectURL(state.preview_URL);
    };
  }, []);

  console.log(state);
  // 

  //
  // const handleSubmit = () => {
  //   if (!state.nickName) {
  //     authorInput.current.focus();
  //     alert("  .");
  //     return;
  //   }
  //   if (!state.nickName.length > 1) {
  //     authorInput.current.focus();
  //     alert(" 2  ");
  //     return;
  //   }
  //   // API  

  //   //  State    

  //   setState({
  //     profileImg: "",
  //     nickName: "",
  //     statueMessage: "",
  //   });

  //   //  ( .     .)

  //   setState({
  //     ...state,
  //     nickName: "",
  //   });
  // };
  const sendImageToServer = async () => {
    if (state.image_file) {
      const formData = new FormData();
      // formData.append("file", state.image_file);
      formData.append("file", state);
      for (var key of formData.keys()) {
        console.log(key);
      }
      for (var value of formData.values()) {
        console.log(value);
      }
      console.log(state);
      console.log(formData);
      await axios.post("/api/image/upload", formData);
      alert("  !");
      setState({
        image_file: "",
        preview_URL: "/assets/unknown.png",
        nickName: "",
        statusMessage: "",
      });
    } else {
      alert(" !");
    }
  };

  return (
    <div>
      <TopBar></TopBar>
        
      <Card
        sx={{
          maxWidth: "70%",
          margin: "auto",
          marginTop: "20%",
        }}
      >
        <div className="uploader-wrapper">
          <input
            type="file"
            accept="image/*"
            onChange={saveImage}
            //    file input value       
            //               !
            onClick={(e) => (e.target.value = null)}
            ref={(refParam) => (inputRef = refParam)}
            style={{ display: "none" }}
          />
          <div className="img-wrapper">
            <Avatar
              className={classes.profileImage}
              src={state.preview_URL}
              onClick={() => inputRef.click()}
            ></Avatar>
          </div>

          <div className="upload-button">
            <button color="error" variant="contained" onClick={deleteImage}>
               
            </button>
          </div>
        </div>
        <div style={{ paddingTop: "5%", paddingLeft: "5%", fontSize: 24 }}>
          
        </div>
        <div style={{ display: "flex" }}>
          <TextField
            ref={nickNameInput}
            name="nickName"
            value={state.nickName}
            color="error"
            onChange={handleChangeState}
            placeholder=" 10 "
            inputProps={{
              maxLength: 10,
              style: { fontFamily: "GangwonEduAll" },
            }}
            style={{ margin: "auto", display: "flex", width: "90%" }}
          />
        </div>
        <div style={{ paddingTop: "5%", paddingLeft: "5%", fontSize: 24 }}>
           
        </div>
        <div style={{ display: "flex" }}>
          <TextField
            ref={statusMessageInput}
            name="statusMessage"
            value={state.statueMessage}
            onChange={handleChangeState}
            multiline
            rows={4}
            placeholder=" 20 "
            color="error"
            inputProps={{
              maxLength: 20,
              style: { fontFamily: "GangwonEduAll" },
            }}
            style={{ margin: "auto", width: "90%", display: "flex" }}
          />
        </div>

        <Button
          variant="contained"
          style={{
            display: "flex",
            margin: "auto",
            width: "90%",
            height: "40px",
            backgroundColor: "#FFB9B9",
            marginBottom: "10%",
            marginTop: "10%",
          }}
          onClick={sendImageToServer}
        >
          <p style={{ fontSize: "150%", fontFamily: "GangwonEduAll" }}>
            
          </p>
        </Button>

        {/* <input
          ref={nickNameInput}
          name="nickName"
          value={state.nickName}
          onChange={handleChangeState}
          maxLength={10}
        />
          <textarea
            ref={statusMessageInput}
            name="statusMessage"
            value={state.statueMessage}
            onChange={handleChangeState}
          /> */}
      </Card>
    </div>
  );
};

export default SignUp;
